/*
    General CViTjs config
    #cvit-app is the wrapper div for the entire
    cvit application
*/

#selector-app {
    max-width: 85%;
}

.genotype-field {
    border-width: 1px;
    border-style: solid;
    border-color: dimgrey;
    padding: 0.5em 1em;
}


.select__value-container{
    height: 2rem;
    min-height: 2rem;
    max-height: 2rem;
}

.select__input{
    max-height: 1rem;
}

.git-option{
    margin-top:.25rem;
    margin-bottom: 1.25rem;
}

.genotype-select {
    width: 100%;
    padding-bottom: 0.5rem;
}


legend {
    margin: 0.5em;
    text-align: left;
}

.fake-button {
    display: inline-block;
    height: 38px;
    padding: 0 30px;
    text-align: center;
    font-size: 11px;
    font-weight: 600;
    line-height: 38px;
    letter-spacing: .1rem;
    text-transform: uppercase;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border-radius: 4px;
    border: 1px solid #888;
    color: #333;
    cursor: pointer;
    box-sizing: border-box;
}

.remove-genotype {
    margin-top: 2em;
    text-align: center;
}

